import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Navbar from "../layouts/navbar.vue";
import Footer from "../layouts/footer.vue";
import Login from "../views/login.vue";
import SingUp from "../views/singup.vue";
import Vapes from "../views/allproducts.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    components: { default: Home, navbar: Navbar, footer: Footer },
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue"),
  },
  {
    path: "/login",
    name: "Login",
    components: { default: Login, navbar: Navbar, footer: Footer },
  },
  {
    path: "/singup",
    name: "Sing Up",
    components: { default: SingUp, navbar: Navbar, footer: Footer },
  },
  {
    path: "/vapes",
    name: "Vapes",
    components: { default: Vapes, navbar: Navbar, footer: Footer },
  },
  {
    path: "/product",
    name: "Producto",
    components: {
      default: () => import("../views/product.vue"),
      navbar: Navbar,
      footer: Footer,
    },
  },
  {
    path: "/404",
    redirect: "/",
  },
];

const router = new VueRouter({
  mode: 'history',
  routes,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
});

export default router;
